section{
    max-width: 100vw;
    height: 87vh;
    background-color: #252420;
    color: white;
    display: flex;
}
section>div{
    width: 50%;
    border: solid rgba(255, 0, 0, 0);
}
section>div>h1{
    font-size: 2.5vw;
    margin-left: 200px;
    margin-top: 140px;
}
section>div>h3{
    margin-top: 40px;
    margin-left: 200px;
}

section>div>button{
    margin-left: 200px;
    border: none;
    margin-top: 40px;
    padding: 10px;
    background-color: #FF7500;
    color: white;
    border-radius: 5px;
    
    
}